<template>
    <nav class="nav flex-row-between">
        <a href="https://scoutdoge.com/#/" target="_blank" rel="noopener noreferrer">
            <img src="~@/assets/images/common/logo.png" class="nav-logo"/>
        </a>
        
        <div class="nav-list flex-1 flex-row-start">
            <div 
                class="flex-row-center nav-list__item pointer" 
                v-for="(item, index) in navList" 
                :key="item.name"
                @click="togglePage(item.path)">
                <img :src="item.activeIcon" class="nav-list__icon" v-if="item.path == $route.path"/>
                <img :src="item.icon" class="nav-list__icon" v-else/>
                <span class="nav-list__text text-white" :class="{'active': item.path == $route.path}">{{item.name}}</span>
            </div>
            <!-- <el-dropdown trigger="click">
                <div class="flex-row-center nav-list__item pointer">
                    <img src="~@/assets/images/common/more.png" class="nav-list__icon" />
                    <span class="nav-list__text text-white">More</span>
                    <img src="~@/assets/images/common/arrow.png"  class="nav-list__arrow"/>
                </div>
                <template #dropdown>
                    <el-dropdown-menu>
                        <el-dropdown-item 
                            class="flex-row-start nav-list__item select pointer" 
                            v-for="item in moreList" 
                            :key="item.name"
                            @click="togglePage(item.path)">
                            <img :src="item.icon" class="nav-list__icon" />
                            <span class="nav-list__text" :class="{'active': $route.path == item.path}">{{item.name}}</span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </template>
            </el-dropdown> -->
        </div>
        
        <div class="nav-right flex-row-end">
            <div 
                class="nav-login text-center pointer text-16" 
                @click="connectHandler" 
                v-loading="connectLoading"
                v-if="!getAccount">
                Connect
            </div>
            <el-dropdown v-else>
                <div 
                    class="nav-login text-center pointer text-16 flex-row-center" >
                    {{getAccount}}
                    <el-icon color="#fff"><caret-bottom /></el-icon>
                </div>
                <template #dropdown>
                    <el-dropdown-menu>
                        <el-dropdown-item @click="logout">sign out</el-dropdown-item>
                    </el-dropdown-menu>
                </template>
            </el-dropdown>
            <el-dropdown trigger="click">
                <div class="flex-row-center nav-group">
                    English
                    <el-icon color="#fff">
                        <caret-bottom />
                    </el-icon>
                </div>
                <template #dropdown>
                    <el-dropdown-menu>
                        <el-dropdown-item class="nav-group__list flex-row-start">
                            <span>English</span>
                        </el-dropdown-item>
                        <el-dropdown-item class="nav-group__list flex-row-start">
                            <span>简体中文</span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </template>
            </el-dropdown>
        </div>
        <TabBar class="nav-tabbar" :nav-list="navList" :more-list="moreList" @click="togglePage"/>
    </nav>
</template>

<script>
import { CaretBottom } from '@element-plus/icons'
import { ElNotification } from 'element-plus'
import TabBar from './tabbar.vue'
export default {
    components: {
        CaretBottom,
        TabBar
    },
    data() {
        return {
            navList: [{
                name: 'Dashboard',
                icon: require('@/assets/images/common/dashboard.png'),
                activeIcon: require('@/assets/images/common/dashboard_active.png'),
                path: '/Dashboard'
            },{
                name: 'Gem',
                icon: require('@/assets/images/common/gem.png'),
                path: '/Gem'
            },{
                name: 'NFT',
                icon: require('@/assets/images/common/nft.png'),
                path: '/NFT'
            },{
                name: 'Invitation',
                icon: require('@/assets/images/common/gamefi.png'),
                activeIcon: require('@/assets/images/common/gamefi_active.png'),
                path: '/dapp/invitation'
            }],
            moreList: [{
                name: 'GameFi',
                icon: require('@/assets/images/common/gamefi.png'),
                activeIcon: require('@/assets/images/common/gamefi_active.png'),
                path: '/GameFi'
            }],
            account: '',
            connectLoading: false,
        }
    },
    computed: {
        getAccount() {
            if (this.account) {
                return '0x....'+ this.account.substr(-4)
            }
            return ''
        }
    },
    mounted(){
        this.account = this.$wallet.accounts[0]
        this.$wallet.on('accountChange', () => {
            this.account = this.$wallet.accounts[0]
        })
    },
    methods: {
        logout() {
            this.$wallet.logout()
        },
        async connectHandler() {
            this.connectLoading = true
            try {
                await this.$wallet.initNetwork()
                this.connectLoading = false
            } catch (error) {
                this.connectLoading = false
            }
            
        },
        /**
         * 跳转到指定页面
         * @param {String} path 页面路由地址
         */
        togglePage(path) {
            if (path == this.$route.path) {
                this.$router.push({
                    path: '/redirect',
                    query: path
                })
                return
            }
            ElNotification({
                title: 'Coming soon',
                message: 'Coming soon',
            })
            return;
            if (!path) {
                return
            }
            this.$router.push({
                path
            })
        }
    }
}
</script>

